import NavigationItem from 'components/Atoms/NavigationItem/NavigationItem'
import { memo } from 'react'

function NavigationLayout ({
  className,
  list,
  activeItem,
  ...props
}) {
  return list
    .map((item, index) => (
      <li
        className={className}
        key={index}
      >
        <NavigationItem
          isActive={item.url === activeItem}
          {...item}
          {...props}
        />
      </li>
    ))
}

const NavigationLayoutMemo = memo(NavigationLayout)

export default NavigationLayoutMemo
