import { memo, useState } from 'react'
import classnames from 'classnames'
// import Swiper core and required modules
import SwiperCore, { Autoplay, Navigation, Pagination, EffectFade  } from 'swiper'
import { Swiper } from 'swiper/react'
// Import Swiper styles
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/pagination/pagination.scss'
import 'swiper/components/effect-fade/effect-fade.scss'
// import 'swiper/components/autoplay/autoplay.scss'

SwiperCore.use([Autoplay, Navigation, Pagination, EffectFade ])

function SliderProvider ({
  className,
  slides,
  children,
  isButtonsRenderRequired,
  swipeButtonDisabledClass,
  isFade,
  ...props
}) {
  const [activeIndex, setActiveIndex] = useState(0)
  const [buttonPrevRef, setButtonPrevRef] = useState(null)
  const [buttonNextRef, setButtonNextRef] = useState(null)
  const isChildrenFunc = typeof children === `function`
  const slidesPerView = props?.slidesPerView || 1
  
  const extraProps = isFade
    ? {
      fadeEffect: {
        crossFade: true
      }
    }
    : {}
  
  return (
    <>
      <Swiper
        className={classnames(className)}
        onActiveIndexChange={swiper => {
          setActiveIndex(swiper.activeIndex)
        }}
        navigation={{
          prevEl: buttonPrevRef,
          nextEl: buttonNextRef,
        }}
        wrapperTag={ `ul` }
        resizeObserver={true}
        watchOverflow={true}
        effect={isFade ? `fade` : `default`}
        {...props}
        {...extraProps}
      >
        { slides }
      </Swiper>
      { isChildrenFunc
        ? children({
          buttonPrev: setButtonPrevRef,
          buttonNext: setButtonNextRef,
          isFirstSlide: activeIndex === 0,
          isLastSlide: activeIndex === slides.length - slidesPerView
        })
        : children
      }
    </>
  )
}

export default memo(SliderProvider)