import React, { useState, useEffect, useCallback } from 'react'
import css from './ImagePreview.module.scss'
import classnames from 'classnames'
import IconClose from 'assets/icons/IconClose'
import IconSpinner from 'assets/icons/IconSpinner'

function ImagePreview ({
  className,
  file,
  onDelete,
  TagName = `li`,
}) {
  const [preview, setPreview] = useState(null)
  const [isLoading, setLoading] = useState(false)

  const readPreviewUrl = useCallback(() => {
    if (!file) return null

    setLoading(true)
    const reader = new FileReader()
    reader.readAsDataURL(file)

    reader.onload = evt => {
      setPreview(evt.target.result)
      setLoading(false)
    }
  }, [file])

  const renderPreview = useCallback(() => {
    if (isLoading) {
      return <IconSpinner className={css.spinner} />
    }

    return (
      <img
        className={css.preview}
        src={preview}
        alt={ `Preview of file ${file?.name}` }
      />
    )
  }, [isLoading, preview, file.name])


  useEffect(() => {
    readPreviewUrl()
  }, [readPreviewUrl])

  return (
    <TagName className={classnames(css.wrapper, className)}>
      { renderPreview() }
      <span className={css.name}>
        { file?.name }
      </span>
      <button
        onClick={onDelete}
        className={css.button}
        type="button"
      >
        Удалить файл
        <IconClose className={css.icon} />
      </button>
    </TagName>
  )
}

export default ImagePreview
