import css from './AboutCompanyPrinciples.module.scss'
import IllustratedList from 'components/Molecules/IllustratedList/IllustratedList'
import { corpPrinciplesData } from 'utils/dataAbout'
import { useSelector } from 'react-redux'
import AboutCompanyPrinciplesTemplate
  from 'components/Templates/AboutCompany/AboutCompanyPrinciplesTemplate/AboutCompanyPrinciplesTemplate'

function AboutCompanyPrinciples () {
  const lang = useSelector(store => store.ui.lang)

  return (
    <IllustratedList
      className={css.wrapper}
      template={AboutCompanyPrinciplesTemplate}
      {...corpPrinciplesData[lang]}
    />
  )
}

export default AboutCompanyPrinciples
