import React from 'react'
import css from './BlockStyleControls.module.scss'
import classnames from 'classnames'
import IconQuote from 'assets/icons/IconQuote'
import IconUL from 'assets/icons/IconUL'
import IconOL from 'assets/icons/IconOL'
import StyleButton from 'components/Molecules/InputEditor/StyleButton/StyleButton'

function BlockStyleControls ({className, editorState, onToggle, children}) {
  const BLOCK_TYPES = [
    {
      renderIcon: className => <IconQuote className={className} />,
      style: 'blockquote',
      title: `Вставить цитату`
    },
    {
      renderIcon: className => <IconUL className={className} />,
      style: 'unordered-list-item',
      title: `Вставить ненумерованный список`
    },
    {
      renderIcon: className => <IconOL className={className} />,
      style: 'ordered-list-item',
      title: `Вставитб нумерованый список`
    },
    {
      label: 'Заголовок 1',
      style: 'header-one'
    },
    {
      label: 'Заголовок 2',
      style: 'header-two'
    },
    {
      label: 'Заголовок 3',
      style: 'header-three'
    },
  ]

  const selection = editorState.getSelection()
  const blockType = editorState
    .getCurrentContent()
    .getBlockForKey(selection.getStartKey())
    .getType()
  
  return (
    <div className={classnames(css.wrapper, className)}>
      {BLOCK_TYPES.map(({label, style, ...item}, index) =>
        <StyleButton
          active={style === blockType}
          label={label}
          onToggle={blockType => onToggle(blockType)}
          style={style}
          className={classnames({
            [css.buttonHeading]: style.includes(`header`)
          })}
          key={index}
          {...item}
        />
      )}
      { children }
    </div>
  )
}

export default BlockStyleControls
