import AboutCompanyBanner
  from 'components/Organisms/AboutCompany/AboutCompanyBanner/AboutCompanyBanner'
import { useSelector } from 'react-redux'
import AboutCompanyTimeline
  from 'components/Organisms/AboutCompany/AboutCompanyTimeline/AboutCompanyTimeline'
import AboutCompanyInfo from 'components/Organisms/AboutCompany/AboutCompanyInfo/AboutCompanyInfo'
import AboutCompanyPrinciples
  from 'components/Organisms/AboutCompany/AboutCompanyPrinciples/AboutCompanyPrinciples'
import AboutCompanyPartners
  from 'components/Organisms/AboutCompany/AboutCompanyPartners/AboutCompanyPartners'
import AboutCompanyContacts
  from 'components/Organisms/AboutCompany/AboutCompanyContacts/AboutCompanyContacts'
import { isMobile } from 'react-device-detect'
import AboutCompanyPrinciplesAdaptive
  from 'components/Organisms/AboutCompany/AboutCompanyPrinciples/AboutCompanyPrinciplesAdaptive'
import { corpPrinciplesData } from 'utils/dataAbout'

const Home = () => {
  const lang = useSelector(store => store.ui.lang)

  return (
    <>
      <AboutCompanyBanner lang={lang} />
      <AboutCompanyTimeline lang={lang} />
      <AboutCompanyInfo />
      { isMobile
        ? <AboutCompanyPrinciplesAdaptive {...corpPrinciplesData[lang]} />
        : <AboutCompanyPrinciples/>
      }
      {/*<AboutCompanyPartners />*/}
      {/*<AboutCompanyContacts />*/}
    </>
  )
}

export default Home
