import { useEffect, useCallback } from 'react'
import {
  ContentBlock,
  ContentState,
  convertToRaw,
  EditorState,
  genKey,
} from 'draft-js'
import { List } from 'immutable'

function useResetHeadingStyles ({
  onChange,
  observer
}) {
  const clearBlockStyle = useCallback(editorState => {
    console.log(convertToRaw(editorState.getCurrentContent()))
    const newBlock = new ContentBlock({
      key: genKey(),
      type: "unstyled",
      text: "",
      characterList: List()
    })

    const contentState = editorState.getCurrentContent()
    const newBlockMap = contentState.getBlockMap().set(newBlock.getKey(), newBlock)

    return EditorState.push(
      editorState,
      ContentState
        .createFromBlockArray(newBlockMap.toArray())
        .set('selectionAfter', contentState.getSelectionAfter().merge({
          anchorKey: newBlock.getKey(),
          anchorOffset: 0,
          focusKey: newBlock.getKey(),
          focusOffset: 0,
          isBackward: false,
        })),
      "split-block"
    )
  }, [])

  const checkIfLastBlockIsHeader = useCallback(editorState => {
    const blocks = convertToRaw(editorState.getCurrentContent()).blocks
    const lastBlockType = blocks[blocks.length - 1].type

    return lastBlockType.includes(`header`)
  }, [])

  const handlePressEnter = useCallback((evt, editorState) => {
    if (evt.keyCode === 13 || evt.key === `Enter`) {
      const isStyleResetRequired = checkIfLastBlockIsHeader(editorState)

      if (isStyleResetRequired) {
        onChange(clearBlockStyle(editorState))
        return true
      }
    }

    return false
  }, [checkIfLastBlockIsHeader, clearBlockStyle, onChange])
  handlePressEnter.id = `handlePressEnter`

  useEffect(() => {
    const isHandlerExistsInObserver = observer.findIndex(item => item.id === handlePressEnter.id) >= 0

    if (!isHandlerExistsInObserver) {
      observer.push(handlePressEnter)
    }
  }, [observer, handlePressEnter])

}

export default useResetHeadingStyles
