import classnames from 'classnames'
import css from './LangSelectorLayout.module.scss'

function LangSelectorLayout ({
  className,
  list,
  onClick,
  activeLang,
}) {
  function renderItems (list) {
    return list
      .map((lang, index) => (
        <li className={css.item} key={index}>
          <button
            className={classnames(css.button, {
              [css.buttonActive]: activeLang === lang
            })}
            onClick={() => onClick(lang)}
            type="button"
          >
            { lang }
          </button>
        </li>
      ))
  }

  return (
    <ul className={classnames(css.list, className)}>
      { renderItems(list) }
    </ul>
  )
}

export default LangSelectorLayout
