import Contacts from 'components/Molecules/Contacts/Contacts'
import { useSelector } from 'react-redux'
import { contactsData } from 'utils/dataAbout'

function AboutCompanyContacts () {
  const lang = useSelector(store => store.ui.lang)

  return (
    <Contacts {...contactsData[lang]} />
  )
}

export default AboutCompanyContacts
