import React, { memo } from 'react'
import css from './InputImage.module.scss'
import classnames from 'classnames'
import { ACCEPTED_IMAGE_FORMATS } from 'utils/const'
import ImagePreview from 'components/Molecules/ImagePreview/ImagePreview'
import InputFile from 'components/Atoms/InputFile/InputFile'
import MultipleInputLabel from 'components/Organisms/MultipleInputLabel/MultipleInputLabel'
import IconFile from 'assets/icons/IconFile'

const ImageInput = ({
  className,
  onChange,
  value,
  isWithPreview = true,
  isError,
  isMultiple,
  ...props
}) => {
  const formats = ACCEPTED_IMAGE_FORMATS

  function handleDeletePreview (index) {
    const files = [...value]
    const updatedFiles = [...files.slice(0, index), ...files.slice(index + 1)]
    onChange(updatedFiles)
  }

  const renderPreview = () => {
    if (isWithPreview && Array.isArray(value) && value.length > 0) {

      return (
        <ul className={css.list}>
          {value
            .map((file, index) => (
              <ImagePreview
                className={css.item}
                file={file}
                onDelete={() => handleDeletePreview(index)}
                isMultiple={isMultiple}
                key={index}
              />
            ))
          }
        </ul>
      )
    }

    return null
  }

  return (
    <div
      className={classnames(css.wrapper, className, {
        [css.wrapperError]: isError,
      })}
    >
      <InputFile
        formats={formats}
        onChange={onChange}
        value={value}
        isMultiple={isMultiple}
        {...props}
      >
        {isMultiple
          ? (
            <MultipleInputLabel
              files={value}
              onChange={onChange}
              {...props}
            />
          )
          : (
            <span className={css.label}>
              <IconFile className={css.icon} />
              Прикрепить файл
            </span>
          )}
      </InputFile>
      { renderPreview() }
    </div>
  )
}

export default memo(ImageInput)
