import css from './Heading.module.scss'
import classnames from 'classnames'

export const HeadingTypes = {
  H1: `h1`,
  H2: `h2`,
  H3: `h3`,
  H4: `h4`
}

function Heading ({
  className,
  tag = HeadingTypes.H2,
  style = HeadingTypes.H2,
  isLightest,
  isCapitalized,
  children
}) {
  const TagName = tag

  return (
    <TagName
      className={classnames(css.heading, css[style], className, {
        [css.headingLightest]: isLightest,
        [css.headingCapitalized]: isCapitalized,
      })}
      dangerouslySetInnerHTML={{ __html: children }}
    />
  )
}

export default Heading
