import { memo, useCallback, useMemo } from 'react'
import css from './LangSelectorAdaptive.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import classnames from 'classnames'
import { LangOptions } from 'utils/const'
import { setLang } from 'store/actions'

function LangSelectorAdaptive ({
  className
}) {
  const dispatch = useDispatch()
  const activeLang = useSelector(store => store.ui.lang)
  
  const langOptions = useMemo(() => {
    return Object.values(LangOptions).filter(item => item !== activeLang)
  }, [activeLang])
  
  const handleClickLang = useCallback(() => {
    dispatch(setLang(langOptions[0]))
  }, [langOptions, dispatch])
  
  return (
    <button
      onClick={handleClickLang}
      className={classnames(css.button, className)}
      type="button"
    >
      { activeLang }
    </button>
  )
}

export default memo(LangSelectorAdaptive)