import React from 'react'
import css from './StyleButton.module.scss'
import classnames from 'classnames'

function StyleButton ({
  className,
  onToggle,
  style,
  active,
  label,
  renderIcon,
  extStyles = {},
  ...props
}) {
  function handleToggle (evt) {
    evt.preventDefault()
    onToggle(style)
  }

  return (
    <button
      onClick={handleToggle}
      className={classnames(css.button, className, {
        [css.buttonActive]: active
      })}
      type={ `button` }
      style={{...extStyles}}
      {...props}
    >
      { renderIcon && renderIcon(css.icon) }
      { label }
    </button>
  )
}

export default StyleButton
