import React from 'react'
import { RichUtils } from 'draft-js'
import css from 'components/Molecules/InputEditor/InputEditor.module.scss'
import BlockStyleControls from 'components/Molecules/InputEditor/BlockStyleControls/BlockStyleControls'

function useBlockStyleControls ({
  editorState,
  onChange,
}) {
  function toggleBlockType (blockType) {
    onChange(
      RichUtils.toggleBlockType(
        editorState,
        blockType
      )
    )
  }

  function getBlockStyle (block) {
    switch (block.getType()) {
      case `header-one`: return css.h1
      case `header-two`: return css.h2
      case `header-three`: return css.h3
      case `unordered-list-item`: return css.ul
      case `ordered-list-item`: return css.ol
      case 'blockquote': return 'RichEditor-blockquote'
      default: return css.p
    }
  }

  return {
    blockStyleButtonsComponent: (
      <BlockStyleControls
        editorState={editorState}
        onToggle={toggleBlockType}
      />
    ),
    getBlockStyle
  }
}

export default useBlockStyleControls
