import { memo, useEffect, useRef } from 'react'
import css from './HeaderAdaptiveMenu.module.scss'
import AnimationProvider from 'components/Organisms/AnimationProvider/AnimationProvider'
import classnames from 'classnames'

function HeaderAdaptiveMenu ({
  setOpenStatus,
  isOpen,
  children,
  ...props
}) {
  const wrapperRef = useRef(null)
  
  useEffect(() => {
    const handleClickOutside = evt => {
      if (evt.target === wrapperRef.current) {
        setOpenStatus(false)
        // wrapperRef.current.removeEventListener(`click`, handleClickOutside)
      }
    }
    
    if (isOpen && wrapperRef.current) {
      wrapperRef.current.addEventListener(`click`, handleClickOutside)
    }
  }, [isOpen, setOpenStatus])
  
  return (
    <AnimationProvider
      isVisible={isOpen}
      classNameHidden={css.wrapperHidden}
      classNameVisible={css.wrapperVisible}
    >
      {
        ({elementRef, animationClassName}) => (
          <div
            className={classnames(css.wrapper, animationClassName)}
            ref={node => {
              elementRef.current = node
              wrapperRef.current = node
            }}
          >
            <div className={classnames(css.menu)} {...props}>
              { children }
            </div>
          </div>
        )
      }
    </AnimationProvider>
  )
}

export default memo(HeaderAdaptiveMenu)