import { FirebaseErrorCodes } from 'utils/const'

export const debounce = (func, delay = 250) => {
  let isCoolDown = false

  return function () {
    if (isCoolDown) return null
    
    func.apply(this, arguments)
    isCoolDown = true

    setTimeout(() => {
      isCoolDown = false
    }, delay)
  }
}

export const throttle = (func, delay) => {
  let isThrottled = false
  let savedArgs
  let savedThis
  
  function wrapper () {
    if (isThrottled) {
      savedArgs = arguments
      savedThis = this
    }
    
    func.apply(this, arguments)
    
    setTimeout(() => {
      isThrottled = false
      
      if (savedArgs) {
        wrapper.apply(savedThis, savedArgs)
        savedArgs = null
        savedThis = null
      }
    }, delay)
  }
  
  return wrapper
}

export const updateObject = (object, field) => Object.assign({}, object, field)

export function addZeroToSingleDigit (value) {
  return !Number.isNaN(+value) && +value < 10
    ? `0${value}`
    : value
}

export function getDateComponents (timestamp) {
  const date = new Date(timestamp)
  const year = date.getFullYear()
  const month = addZeroToSingleDigit(date.getMonth() + 1)
  const day = date.getDate()
  const hours = addZeroToSingleDigit(date.getHours())
  const minutes = addZeroToSingleDigit(date.getMinutes())
  
  return {
    year,
    month,
    day,
    hours,
    minutes
  }
}

export function processFirebaseErrors (code) {
  const message = FirebaseErrorCodes[code]
  
  return message || code || `An unidentified error has occurred!`
}

export function removeDuplicateFiles (addedFiles, existingFiles) {
  return Array.from(addedFiles)
    .filter(file => {
      return !existingFiles.find(fileState => {
        return fileState.name === file.name && fileState.size === file.size
      })
    })
}