import css from './BlogPost.module.scss'
import Container from 'components/Templates/Container/Container'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { BLOG } from 'utils/routes'
import { dataPost } from 'utils/dataPost'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import { getDateComponents } from 'utils'
import { useSelector } from 'react-redux'

function BlogPost () {
  const isAuth = useSelector(store => store.auth.email)
  
  const data = dataPost
  
  const {
    image,
    heading,
    date,
    content
  } = data
  
  const {
    year,
    month,
    day
  } = getDateComponents(date)
  
  return (
    <section className={css.section}>
      <Container className={css.wrapper}>
        <Button
          className={css.backlink}
          variant={ButtonVariants.ARROW_REVERSE}
          link={BLOG}
        >
          Назад
        </Button>
        <article className={css.content}>
          <header className={css.header}>
            <Heading
              tag={HeadingTypes.H2}
              style={HeadingTypes.H4}
            >
              { heading }
            </Heading>
            <span className={css.date}>
              { `${day}.${month}.${`${year}`.slice(2)}` }
            </span>
          </header>
          <div
            dangerouslySetInnerHTML={{ __html: content }}
            className={css.wyziwig}
          />
          { isAuth &&
            <Button
              onClick={ () => alert(`Go to post edit page`) }
              className={ css.button }
              variant={ ButtonVariants.EDIT }
            >
              Редактировать
            </Button>
          }
        </article>
        <div className={css.frame}>
          <img
            src={image}
            className={css.image}
            alt={heading}
          />
        </div>
      </Container>
    </section>
  )
}

export default BlogPost