import css from './TimelineItem.module.scss'
import classnames from 'classnames'

function TimelineItem ({
  className,
  tag = `li`,
  index,
  data,
  isFirst,
  isLast,
  isEven,
  extStyles = {}
}) {
  const TagName = tag
  const {
    image,
    label,
    description
  } = data

  return (
    <TagName
      className={classnames(css.wrapper, className, {
        [css.wrapperFirst]: isFirst,
        [css.wrapperLast]: isLast,
        [css.wrapperEven]: isEven,
      })}
      data-index={index}
      style={extStyles}
    >
      <span className={css.overlay} />
      <img
        src={image}
        className={css.image}
        alt={ `Energy Standard timeline point - ${label}` }
      />
      <span
        dangerouslySetInnerHTML={{ __html: label }}
        className={css.label}
      />
      <div className={css.frame}>
        <div
          dangerouslySetInnerHTML={{ __html: description }}
          className={css.description}
        />
      </div>
    </TagName>
  )
}

export default TimelineItem
