import css from './AboutCompanyInfo.module.scss'
import IllustratedInfo from 'components/Molecules/IllustratedInfo/IllustratedInfo'
import { aboutInfoData } from 'utils/dataAbout'
import { useSelector } from 'react-redux'
import IconEllipse from 'assets/icons/IconEllipse'
import Container from 'components/Templates/Container/Container'

function AboutCompanyInfo () {
  const lang = useSelector(store => store.ui.lang)

  return (
    <Container>
      <IllustratedInfo
        className={css.wrapper}
        {...aboutInfoData[lang]}
      >
        <IconEllipse className={css.icon} />
      </IllustratedInfo>
    </Container>
  )
}

export default AboutCompanyInfo
