import css from 'components/Templates/AboutCompany/AboutCompanyPrinciplesTemplate/AboutCompanyPrinciplesTemplate.module.scss'
import Container from 'components/Templates/Container/Container'

function AboutCompanyPrinciplesTemplate ({children}) {
  return (
    <Container className={css.wrapper}>
      { children({
        headingClassName: css.heading,
        listClassName: css.description
      }) }
    </Container>
  )
}

export default AboutCompanyPrinciplesTemplate
