import Banner from 'components/Molecules/Banner/Banner'
import { aboutBannerData } from 'utils/dataAbout'
import AnimatedLogo from 'components/Atoms/AnimatedLogo/AnimatedLogo'

function AboutCompanyBanner ({lang}) {
  return (
    <Banner
      data={aboutBannerData[lang]}
    >
      <AnimatedLogo />
    </Banner>
  )
}

export default AboutCompanyBanner
