import HeaderLayout from 'components/Molecules/HeaderLayout/HeaderLayout'
import useHeaderTypeController from 'hooks/useHeaderTypeController'
import Navigation from 'components/Organisms/Navigation/Navigation'
import { useSelector } from 'react-redux'
import LangSelector from 'components/Organisms/LangSelector/LangSelector'
import HeaderContentTemplate from 'components/Templates/HeaderContentTemplate/HeaderContentTemplate'
import LoginButton from 'components/Organisms/LoginButton/LoginButton'

function Header ({setHeaderRef}) {
  const {className} = useHeaderTypeController()
  const lang = useSelector(store => store.ui.lang)

  return (
    <HeaderLayout
      className={className}
      setHeaderRef={setHeaderRef}
    >
      <HeaderContentTemplate>
        {({navClassName, langClassName, loginClassName}) => (
          <>
            <nav className={navClassName}>
              <Navigation lang={lang} />
            </nav>
            <LangSelector className={langClassName}/>
            <LoginButton
              className={loginClassName}
            />
          </>
        )}
      </HeaderContentTemplate>
    </HeaderLayout>
  )
}

export default Header
