import css from 'components/Templates/AboutCompany/AboutCompanyTimelineTemplate/AboutCompanyTimelineTemplate.module.scss'
import Container from 'components/Templates/Container/Container'

function AboutCompanyTimelineTemplate ({children}) {
  return (
    <Container className={css.wrapper}>
      {children({
        listClassName: css.list
      })}
    </Container>
  )
}

export default AboutCompanyTimelineTemplate
