import { memo } from 'react'
import css from './ProjectsListTemplate.module.scss'
import classnames from 'classnames'

function ProjectsListTemplate ({
  className,
  TagName = `ul`,
  children
}) {
  return (
    <TagName className={classnames(css.wrapper, className)}>
      { children }
    </TagName>
  )
}

export default memo(ProjectsListTemplate)