import css from './ProjectsFilter.module.scss'
import classnames from 'classnames'
import Input, {InputVariants} from 'components/Atoms/Input/Input'
import { useCallback, memo } from 'react'
import { updateProjectsQuery } from 'components/Organisms/ProjectsDataProvider/controller/actions'

function ProjectsFilter ({
  className,
  query,
  selectedYear,
  dispatch
}) {
  const handleInputChange = useCallback(evt => {
    dispatch(updateProjectsQuery(evt.target.value))
  }, [dispatch])
  
  return (
    <div className={classnames(css.wrapper, className)}>
      <Input
        value={query}
        onChange={handleInputChange}
        variant={InputVariants.SEARCH}
        placeholder={ `Введите поисковый запрос` }
        className={css.filter}
      />
    </div>
  )
}

export default memo(ProjectsFilter)