import { useState, useRef, useEffect, useCallback } from 'react'

function useIntersectionObserver ({
  elementRef,
  margins = '0% 0% 0% 0%',
  threshold = 1.0,
  triggerCriteria = true
}) {
  const [isVisible, setVisibility] = useState(false)
  const observer = useRef(null)
  const element = elementRef.current

  const handleIntersection = useCallback(entries => {
    entries.forEach(entry => {
      setVisibility(entry.isIntersecting)
    })
  }, [])

  const unsubscribeObserver = useCallback(() => {
    if (element && observer.current) {
      observer.current.unobserve(element)
      observer.current = null
    }
  }, [element])


  useEffect(() => {
    const observedNode = elementRef.current
    if (observedNode && !observer.current && triggerCriteria) {
      observer.current = new IntersectionObserver(handleIntersection, {
        rootMargin: margins,
        threshold
      })

      observer.current.observe(observedNode)
    }

    return () => {
      if (observedNode && observer.current) {
        observer.current.unobserve(observedNode)
        observer.current = null
      }
    }
  }, [elementRef, handleIntersection, margins, threshold, triggerCriteria])

  return {
    isVisible,
    unsubscribeObserver
  }
}

export default useIntersectionObserver
