import React from 'react'
import { Route, Switch } from 'react-router-dom'
import Home from 'Pages/Home/Home'
import Blog from 'Pages/Blog/Blog'
import Projects from 'Pages/Projects/Projects'
import {
  BLOG, BLOG_POST_CREATE,
  HOME_PAGE,
  PRODUCTS,
  PROJECTS
} from 'utils/routes'
import Products from './Products/Products'
import Product from 'Pages/Product/Product'
import BlogPost from 'Pages/BlogPost/BlogPost'
import CreatePost from 'Pages/CreatePost/CreatePost'

// TODO apply async import of page components to split the initial js chunk
const Routes = ({isAuth}) => {
  return (
    <Switch>
      <Route exact path={HOME_PAGE} component={Home} />
      <Route exact path={PROJECTS} component={Projects} />
      <Route exact path={PRODUCTS} component={Products} />
      <Route exact path={`${PRODUCTS}/:id`} component={Product} />
      <Route exact path={`${PRODUCTS}/trafo`} component={null} />
      <Route exact path={`${PRODUCTS}/ais`} component={null} />
      <Route exact path={`${PRODUCTS}/cts`} component={null} />
      <Route exact path={`${PRODUCTS}/iis`} component={null} />
      <Route exact path={`${PRODUCTS}/generation`} component={null} />
      <Route exact path={BLOG} component={Blog} />
      <Route exact path={`${BLOG}/:id`} component={BlogPost} />
      { isAuth &&
        <Route exact path={BLOG_POST_CREATE} component={CreatePost} />
      }
    </Switch>
  )
}

export default Routes
