import { Fragment, useMemo } from 'react'
import { Controller } from 'react-hook-form'
import Input from 'components/Atoms/Input/Input'
import { FormItemTypes } from 'utils/const'
import { ContentState, EditorState, convertFromHTML } from 'draft-js'
import InputEditor from 'components/Molecules/InputEditor/InputEditor'
import InputImage from 'components/Atoms/InputImage/InputImage'
// import SelectDropdown from 'components/atoms/SelectDropdown/SelectDropdown'
// import Datepicker from 'components/atoms/Datepicker/Datepicker'
// import { FormItemTypes } from 'utils/const'
// import HorizontalSelector from 'components/atoms/HorizontalSelector/HorizontalSelector'

function useRenderFormItems ({ list, control, setValue  }) {
  return useMemo(() => list.map(({
    element,
    name,
    validation = {},
    defaultValue,
    customOnChange,
    ...item
  }, index) => {
    switch (element) {
      case FormItemTypes.IMAGE:
        return (
          <Controller
            render={({
              field: {
                name,
                onChange,
                value,
                ref
              },
              fieldState: { error }
            }) => (
              <InputImage
                name={name}
                value={value}
                onChange={onChange}
                isError={error}
                formRef={ref}
                setValue={setValue}
                {...item}
              />
            )}
            control={control}
            rules={{
              ...validation
            }}
            name={name}
            defaultValue={defaultValue}
            key={index}
          />
        )
      case FormItemTypes.EDITOR:
        const defaultEditorBlocks = defaultValue
          ? convertFromHTML(defaultValue)
          : convertFromHTML(``)
        const defaultState = ContentState.createFromBlockArray(
          defaultEditorBlocks.contentBlocks,
          defaultEditorBlocks.entityMap
        )
    
        return (
          <Controller
            render={({
              field: {
                name,
                onChange,
                value
              }
           }) => (
              <InputEditor
                name={name}
                value={value}
                onChange={onChange}
                {...item}
              />
            )}
            control={control}
            defaultValue={EditorState.createWithContent(defaultState)}
            rules={{
              ...validation
            }}
            name={name}
            key={index}
          />
        )
      // case FormItemTypes.DUAL_SELECTOR: {
      //   return (
      //     <Fragment key={name}>
      //       <Controller
      //         render={({field: { value, onChange }}) => (
      //           <HorizontalSelector
      //             value={value}
      //             onChange={onChange}
      //             {...item}
      //           />
      //         )}
      //         name={name}
      //         control={control}
      //         defaultValue={defaultValue}
      //       />
      //     </Fragment>
      //   )
      // }
      // case FormItemTypes.DATEPICKER: {
      //   return (
      //     <Fragment key={name}>
      //       <Controller
      //         render={({
      //           field: { ref, ...field },
      //           fieldState: { error }
      //         }) => (
      //           <Datepicker
      //             {...field}
      //             {...item}
      //           />
      //         )}
      //         control={control}
      //         rules={validation}
      //         name={name}
      //         defaultValue={defaultValue ?? null}
      //       />
      //     </Fragment>
      //   )
      // }
      // case FormItemTypes.SELECT: {
      //   const { options } = item
      //
      //   return (
      //     <Fragment key={name}>
      //       <Controller
      //         render={({
      //           field: { ref, ...field },
      //           fieldState: { error }
      //         }) => (
      //           <SelectDropdown
      //             options={options}
      //             innerRef={ref}
      //             {...field}
      //             {...item}
      //           />
      //         )}
      //         control={control}
      //         rules={validation}
      //         name={name}
      //         defaultValue={defaultValue ?? null}
      //       />
      //     </Fragment>
      //   )
      // }
      default: {
        return (
          <Fragment key={name}>
            <Controller
              render={({
                  field: { ref, onChange, ...field },
                  fieldState: { error }
              }) => (
                <Input
                  onChange={evt => {
                    onChange(evt)
                    customOnChange && customOnChange(evt)
                  }}
                  error={error?.message ?? error}
                  formRef={ref}
                  {...field}
                  {...item}
                />
              )}
              control={control}
              rules={{...validation}}
              name={name}
              defaultValue={defaultValue ?? ``}
            />
          </Fragment>
        )
      }
    }
  }), [list, control, setValue])
}

export default useRenderFormItems
