import produce from 'immer'
import { INIT_FIREBASE } from 'store/actions/actionTypes'

const INITIAL_FIREBASE_STATE = {
  app: null,
}

export const firebaseReducer = produce((draft = INITIAL_FIREBASE_STATE, { type, payload }) => {
  switch (type) {
    case INIT_FIREBASE: {
      draft.app = payload
      return draft
    }
    default: {
      return draft
    }
  }
})
