import css from './FooterLayout.module.scss'
import classnames from 'classnames'

function FooterLayout ({
  wrapperClassName
}) {
  return (
    <div className={classnames(css.footer, wrapperClassName)}>
      <ul className={css.list}>
        <li className={css.item}>
          <a
            className={css.link}
            href="https://www.behance.net/katyagrishck"
            target="_blank"
            rel="noreferrer noopener"
          >
            Design
          </a>
        </li>
        <li className={css.item}>
          <a
            className={css.link}
            href="https://slavenzka.github.io/portfolio/"
            target="_blank"
            rel="noreferrer noopener"
          >
            Development
          </a>
        </li>
      </ul>
      <span className={css.copyright}>
        © Energy Standard 2008 - 2021
      </span>
    </div>
  )
}

export default FooterLayout
