import { memo, useCallback, useMemo } from 'react'
import css from './SliderPreviews.module.scss'
import classnames from 'classnames'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import SliderProvider from 'components/Molecules/SliderProvider/SliderProvider'
import { SwiperSlide } from 'swiper/react'
import { useDispatch } from 'react-redux'
import { toggleModal } from 'store/actions'

function SliderPreviews ({
  className,
  heading,
  list,
  sliderProps = {},
  slidesPerView = 4
}) {
  const dispatch = useDispatch()
  const isControlsRequired = list.length > slidesPerView
  
  const handleClickPreview = useCallback((image, label) => {
    dispatch(toggleModal((
      <img
        className={css.fullImage}
        src={image}
        alt={label}
      />
    ), {
      isContentOnly: true
    }))
  }, [dispatch])
  
  const slides = useMemo(() => {
    return list.map(({image, label}, index) => (
      <SwiperSlide key={index}>
        <button
          onClick={() => handleClickPreview(image, label)}
          className={css.slideButon}
          type="button"
        >
          <img
            src={image}
            className={css.image}
            alt={label}
          />
        </button>
      </SwiperSlide>
    ))
  }, [list, handleClickPreview])
  
  return (
    <div className={classnames(css.wrapper, className)}>
      <Heading
        tag={HeadingTypes.H2}
        style={HeadingTypes.H3}
        isCapitalized
      >
        { heading }
      </Heading>
      <SliderProvider
        slides={slides}
        speed={1000}
        slidesPerView={4}
        className={css.slider}
        {...sliderProps}
      >
        {
          ({buttonPrev, buttonNext, isFirstSlide, isLastSlide}) => isControlsRequired
            ? (
              <>
                <Button
                  className={css.button}
                  elRef={buttonPrev}
                  variant={ButtonVariants.SLIDER_PREV_DARK}
                  isDisabled={isFirstSlide}
                >
                  Slide prev
                </Button>
                <Button
                  className={classnames(css.button, css.buttonNext)}
                  elRef={buttonNext}
                  variant={ButtonVariants.SLIDER_NEXT_DARK}
                  isDisabled={isLastSlide}
                >
                  Slide next
                </Button>
              </>
            )
            : null
        }
      </SliderProvider>
    </div>
    )
}

export default memo(SliderPreviews)