import { memo, useMemo } from 'react'
import css from './AboutCompanyPrinciplesAdaptive.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import SliderProvider from 'components/Molecules/SliderProvider/SliderProvider'
import { SwiperSlide } from 'swiper/react'
import classnames from 'classnames'
import IconSliderArrow from 'assets/icons/IconSliderArrow'

function AboutCompanyPrinciplesAdaptive ({
  heading,
  list
}) {
  const slides = useMemo(() => {
    return list.map(({name, value}, index) => (
      <SwiperSlide tag="li" key={index}>
        <div className={css.item}>
          <Heading
            className={css.heading}
            tag={HeadingTypes.H3}
            style={HeadingTypes.H3}
            isLightest
          >
            { name }
          </Heading>
          <p className={css.description}>
            { value }
          </p>
        </div>
      </SwiperSlide>
    ))
  }, [list])
  
  return (
    <section>
      <Container className={css.wrapper}>
        <Heading>
          { heading }
        </Heading>
      </Container>
      <div className={css.sliderWrapper}>
        <SliderProvider
          className={css.slider}
          slides={slides}
          speed={1000}
          isFade
        >
          {
            ({buttonPrev, buttonNext, isFirstSlide, isLastSlide}) => (
              <>
                <button
                  className={classnames(css.button, {
                    [css.buttonDisabled]: isFirstSlide
                  })}
                  ref={buttonPrev}
                  type="button"
                >
                  <IconSliderArrow className={css.icon} />
                  Prev slide
                </button>
                <button
                  className={classnames(css.button, css.buttonNext, {
                    [css.buttonDisabled]: isLastSlide
                  })}
                  ref={buttonNext}
                  type="button"
                >
                  <IconSliderArrow className={css.icon} />
                  Next slide
                </button>
              </>
            )
          }
        </SliderProvider>
      </div>
    </section>
  )
}

export default memo(AboutCompanyPrinciplesAdaptive)