import React, { useState, useRef, useEffect, useCallback } from 'react'
import css from './MultipleInputLabel.module.scss'
import classnames from 'classnames'
import { removeDuplicateFiles } from 'utils'

function MultipleInputLabel ({
  files = [],
  onChange,
  label = `Прикрепить файл`,
}) {
  const [isDragging, setDragging] = useState(false)
  const [isOverlayVisible, setOverlayVisibility] = useState(false)
  const overlayRef = useRef(null)

  const addFiles = useCallback(addedFiles => {
    onChange([...files, ...addedFiles])
  }, [files, onChange])


  const handleDragIn = useCallback(evt => {
    evt.preventDefault()
    evt.stopPropagation()

    if (!isOverlayVisible) {
      setOverlayVisibility(true)
    }

    if (evt?.dataTransfer?.items && evt.dataTransfer.items.length > 0) {
      setDragging(true)
    }
  }, [isOverlayVisible])

  const handleDragOut = useCallback(evt => {
    evt.preventDefault()
    evt.stopPropagation()
    setDragging(false)
    setOverlayVisibility(false)
  }, [])

  const handleDrag = useCallback(evt => {
    evt.preventDefault()
    evt.stopPropagation()

    if (!isOverlayVisible) {
      setOverlayVisibility(true)
    }
  }, [isOverlayVisible])

  const handleDrop = useCallback(evt => {
    evt.preventDefault()
    evt.stopPropagation()

    setDragging(false)
    setOverlayVisibility(false)

    if (evt.dataTransfer.files && evt.dataTransfer.files.length > 0) {
      const addedFiles = removeDuplicateFiles(evt.dataTransfer.files, files)

      if (addedFiles.length > 0) {
        addFiles(addedFiles)
      }
    }
  }, [addFiles, files])

  const handleDragEventGlobal = useCallback(() => {
    if (!isOverlayVisible) {
      setOverlayVisibility(true)
    }
  }, [isOverlayVisible])

  useEffect(() => {
    const overlay = overlayRef.current

    overlay.addEventListener(`dragenter`, handleDragIn)
    overlay.addEventListener(`dragleave`, handleDragOut)
    overlay.addEventListener(`dragover`, handleDrag)
    overlay.addEventListener(`drop`, handleDrop)
    window.addEventListener('dragover', handleDragEventGlobal)

    return () => {
      overlay.removeEventListener(`dragenter`, handleDragIn)
      overlay.removeEventListener(`dragleave`, handleDragOut)
      overlay.removeEventListener(`dragover`, handleDrag)
      overlay.removeEventListener(`drop`, handleDrop)
      window.removeEventListener('dragover', handleDragEventGlobal)
    }
  }, [
    isOverlayVisible,
    handleDragIn,
    handleDragOut,
    handleDrag,
    handleDrop,
    handleDragEventGlobal,
  ])

  return (
    <span
      className={classnames(css.wrapper, {
        [css.wrapperDraggedOver]: isDragging
      })}
    >
      <span
        className={classnames(css.overlay, {
          [css.overlayVisible]: isOverlayVisible,
        })}
        ref={overlayRef}
      />
      <span>
        { label }
      </span>
    </span>
  )
}

export default MultipleInputLabel
