import { memo } from 'react'
import css from './ProductMore.module.scss'
import IllustratedInfo from 'components/Molecules/IllustratedInfo/IllustratedInfo'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import Container from 'components/Templates/Container/Container'

function ProductMore ({
  className,
  heading,
  description,
  buttonLabel,
  image
}) {
  return (
    <section className={className}>
      <Container>
        <IllustratedInfo
          className={css.wrapper}
          heading={heading}
          description={description}
          controls={(
            <Button
              link={`#`}
              variant={ButtonVariants.FILLED}
              className={css.button}
            >
              { buttonLabel }
            </Button>
          )}
        >
          <img
            src={image}
            className={css.image}
            alt={heading}
          />
        </IllustratedInfo>
      </Container>
    </section>
  )
}

export default memo(ProductMore)