import produce from 'immer'
import { LOGGING_IN_STATUS, UPDATE_AUTH_DATA } from 'store/actions/actionTypes'

const INITIAL_AUTH_STATE = {
  email: null,
  isLoggingIn: false
}

export const authReducer = produce((draft = INITIAL_AUTH_STATE, {type, payload})  => {
  switch (type) {
    case UPDATE_AUTH_DATA: {
      const { email } = payload

      draft.email = email

      return draft
    }
    case LOGGING_IN_STATUS: {
      draft.isLoggingIn = Boolean(payload)
      return draft
    }
    default: {
      return draft
    }
  }
})

