import { memo, useMemo } from 'react'
import css from './ProjectsSlider.module.scss'
import classnames from 'classnames'
import SliderProvider from 'components/Molecules/SliderProvider/SliderProvider'
import {projectsSlider} from 'utils/dataProjects'
import {SwiperSlide} from 'swiper/react'
import ProjectSlide from 'components/Molecules/ProjectSlide/ProjectSlide'
import Button, {ButtonVariants} from 'components/Atoms/Button/Button'

function ProjectsSlider () {
  const data = projectsSlider
  
  const slides = useMemo(() => {
    return data
      .map((item, index  ) => (
        <SwiperSlide
          tag={`li`}
          key={index}
        >
          <ProjectSlide {...item} />
        </SwiperSlide>
      ))
  }, [data])
  
  return (
    <section
      className={css.wrapper}
    >
      <SliderProvider
        swipeButtonDisabledClass={css.disabled}
        isButtonsRenderRequired={data.length > 1}
        slides={slides}
        autoplay={{
          delay: 5000
        }}
      >
        {({buttonPrev, buttonNext, isFirstSlide, isLastSlide}) => (
          <>
            <Button
              className={css.button}
              elRef={buttonPrev}
              variant={ButtonVariants.SLIDER_PREV}
              isDisabled={isFirstSlide}
            >
              Slide prev
            </Button>
            <Button
              className={classnames(css.button, css.buttonNext)}
              elRef={buttonNext}
              variant={ButtonVariants.SLIDER_NEXT}
              isDisabled={isLastSlide}
            >
              Slide next
            </Button>
          </>
        )}
      </SliderProvider>
    </section>
  )
}

export default memo(ProjectsSlider)