import { memo } from 'react'
import css from './BlogTemplate.module.scss'
import classnames from 'classnames'

function BlogTemplate ({
  className,
  children,
  TagName = `ul`
}) {
  return (
    <TagName className={classnames(css.wrapper, className)}>
      { children }
    </TagName>
  )
}

export default memo(BlogTemplate)