import css from './IllustratedInfo.module.scss'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import classnames from 'classnames'

function IllustratedInfo ({
  className,
  heading,
  description,
  children,
  controls
}) {
  return (
    <div
      className={classnames(css.wrapper, className)}
    >
      { children }
      <Heading
        className={css.heading}
        style={HeadingTypes.H3}
        isCapitalized
      >
        { heading }
      </Heading>
      <Description className={css.description}>
        { description }
      </Description>
      {controls &&
        <div className={css.controls}>
          { controls }
        </div>
      }
    </div>
  )
}

export default IllustratedInfo
