import {useSelector} from 'react-redux'
import BannerProducts from 'components/Molecules/BannerProducts/BannerProducts'
import {dataProducts} from 'utils/dataProducts'

function Products () {
  const lang = useSelector(store => store.ui.lang)
  
  return (
    <section>
      <BannerProducts {...dataProducts[lang]} />
    </section>
  )
}

export default Products