import { memo, useCallback, useMemo } from 'react'
import css from './ModalLogin.module.scss'
import classnames from 'classnames'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import { FormErrorTypes, FormItemTypes } from 'utils/const'
import Form from 'components/Organisms/Form/Form'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { signInUser } from 'utils/authHelpers'
import { getAuth } from 'firebase/auth'
import { processFirebaseErrors } from 'utils'
import { useDispatch, useSelector } from 'react-redux'
import { setLoggingIn, toggleModal } from 'store/actions'
import ModalTemplate from 'components/Templates/ModalTemplate/ModalTemplate'

function ModalLogin ({
  className
}) {
  const isLoading = useSelector(store => store.auth.isLoggingIn)
  const dispatch = useDispatch()
  
  const formDescription = useMemo(() => {
    return {
      email: {
        element: FormItemTypes.INPUT,
        name: `email`,
        validation: {
          required: `Login is required`
        },
        defaultValue: ``,
        placeholder: `E-mail`
      },
      password: {
        element: FormItemTypes.INPUT,
        name: `password`,
        validation: {
          required: `Password is required`
        },
        defaultValue: ``,
        placeholder: `Введите пароль`,
        type: `password`
      },
    }
  }, [])
  
  const submitForm = useCallback((data, { setError }) => {
    dispatch(setLoggingIn(true))
    
    const auth = getAuth()
  
    const {
      email,
      password,
    } = data
  
    signInUser({
      auth,
      email,
      password,
      onError: error => {
        setError(formDescription.email.name, {
          type: FormErrorTypes.MANUAL,
          message: processFirebaseErrors(error)
        })
        dispatch(setLoggingIn(false))
      }
    })
      .then(() => {
        dispatch(setLoggingIn(false))
        dispatch(toggleModal(null))
      })
  }, [dispatch, formDescription])
  
  return (
    <ModalTemplate className={classnames(css.wrapper, className)}>
      <Heading
        tag={HeadingTypes.H3}
        style={HeadingTypes.H4}
      >
        Вход в личный кабинет
      </Heading>
      <Form
        data={Object.values(formDescription)}
        submitForm={submitForm}
      >
        {({items}) => (
          <div className={css.form}>
            { items }
            <Button
              className={css.button}
              variant={ButtonVariants.FILLED}
              isLoading={isLoading}
              type="submit"
            >
              Войти
            </Button>
          </div>
        )}
      </Form>
    </ModalTemplate>
  )
}

export default memo(ModalLogin)