import produce from 'immer'
import {
  UPDATE_PROJECTS_DATA,
  UPDATE_PROJECTS_QUERY,
  UPDATE_PROJECTS_YEAR
} from 'components/Organisms/ProjectsDataProvider/controller/actionTypes'

export const projectsReducer = produce((draft, {type, payload}) => {
  switch (type) {
    case UPDATE_PROJECTS_DATA: {
      draft.data = payload
      return draft
    }
    case UPDATE_PROJECTS_QUERY: {
      draft.query = payload
      return draft
    }
    case UPDATE_PROJECTS_YEAR: {
      draft.selectedYear = payload
      return draft
    }
    default: {
      return draft
    }
  }
})