import { memo } from 'react'
import css from 'components/Templates/RibbonTemplate/RibbonTemplate.module.scss'
import classnames from 'classnames'
import { Parallax } from 'react-scroll-parallax'

function RibbonTemplate ({
  className,
  image,
  children
}) {
  return (
    <section
      className={classnames(css.wrapper, className)}
    >
      <Parallax
        y={[`-150px`, `150px`]}
        styleOuter={{
          top: `-10rem`,
          left: `0`,
          width: `100%`,
          position: `absolute`
        }}
      >
        <div
          className={css.overlay}
          style={{
            backgroundImage: `url("${image}")`,
          }}
        />
      </Parallax>
      { children }
    </section>
  )
}

export default memo(RibbonTemplate)