import {memo, useCallback, useMemo, useState} from 'react'
import css from './BannerProducts.module.scss'
import classnames from 'classnames'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import { Link } from 'react-router-dom'

function BannerProducts ({
  className,
  heading,
  background,
  list
}) {
  const [isOpened, toggleOpened] = useState(false)
  
  const navItems = useMemo(() => {
    return list
      .map(({label, url}, index) => (
        <li className={css.item} key={index}>
          <Link
            className={classnames(css.link, css.indicator)}
            to={url}
          >
            { label }
          </Link>
        </li>
      ))
  }, [list])
  
  const handleClickButton = useCallback(() => {
    toggleOpened(prevState => !prevState)
  }, [])
  
  return (
    <div
      className={classnames(css.wrapper, className)}
      style={{
        backgroundImage: `url("${background}")`
      }}
    >
      <Container className={css.container}>
        <ul
          className={classnames(css.list, {
            [css.listOpened]: isOpened
          })}
        >
          { navItems }
        </ul>
        <Heading
          className={css.heading}
          isLightest
        >
          { heading }
        </Heading>
        <button
          onClick={handleClickButton}
          className={classnames(css.button, css.indicator)}
          type="button"
        >
          { heading }
        </button>
      </Container>
    </div>
  )
}

export default memo(BannerProducts)