import { useRef, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setGlobalPadding } from 'store/actions'

function useGlobalHeaderPadding () {
  const fontSize = useSelector(store => store.elastic.curFontSize)
  const headerRef = useRef(null)
  const dispatch = useDispatch()

  function setHeaderRef (node) {
    headerRef.current = node
  }

  useEffect(() => {
    if (headerRef.current) {
      dispatch(setGlobalPadding(headerRef.current.getBoundingClientRect().height))
    }
  }, [dispatch, fontSize])

  return {
    setHeaderRef,
  }
}

export default useGlobalHeaderPadding
