import Timeline from 'components/Molecules/Timeline/Timeline'
import { aboutTimelineData } from 'utils/dataAbout'
import AboutCompanyTimelineTemplate
  from 'components/Templates/AboutCompany/AboutCompanyTimelineTemplate/AboutCompanyTimelineTemplate'

function AboutCompanyTimeline ({lang}) {
  return (
    <Timeline
      list={aboutTimelineData.list[lang]}
      heading={aboutTimelineData.heading[lang]}
      template={AboutCompanyTimelineTemplate}
    />
  )
}

export default AboutCompanyTimeline
