import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getAuth, onAuthStateChanged } from 'firebase/auth'
import { updateAuthData } from 'store/actions'

function useAuthStatus() {
  const userStore = useSelector(store => store.auth)
  const [isInitialCheckDone, setInitialCheckStatus] = useState(false)
  const dispatch = useDispatch()

  useEffect(() => {
    const auth = getAuth()

    onAuthStateChanged(auth, user => {
      if (user) {
        const email = user.email

        dispatch(updateAuthData({
          email,
        }))
      }

      if (!user) {
        dispatch(updateAuthData({
          email: null,
        }))
      }

      setInitialCheckStatus(true)
    })
  }, [dispatch])

  return {
    isAuth: !!userStore?.email,
    isInitialCheckDone
  }
}

export default useAuthStatus
