import React from 'react'
import css from './InlineStyleControls.module.scss'
import classnames from 'classnames'
import StyleButton from 'components/Molecules/InputEditor/StyleButton/StyleButton'

function InlineStyleControls ({
  editorState,
  onToggle,
  children
}) {
  const INLINE_STYLES = [
    {
      label: 'Ж',
      style: 'BOLD',
      extStyles: {
        fontWeight: `700`
      }
    },
    {
      label: 'К',
      style: 'ITALIC',
      extStyles: {
        fontWeight: 700,
        fontStyle: `italic`
      }
    },
    {
      label: 'П',
      style: 'UNDERLINE',
      extStyles: {
        fontWeight: 700,
        textDecoration: `underline`
      }
    },
    // {label: 'Monospace', style: 'CODE'},
  ]

  const currentStyle = editorState.getCurrentInlineStyle()

  return (
    <div className={classnames(css.wrapper)}>
      {INLINE_STYLES.map(({label, style, ...item}) =>
        <StyleButton
          key={label}
          active={currentStyle.has(style)}
          label={label}
          onToggle={onToggle}
          style={style}
          className={css.button}
          {...item}
        />
      )}
      { children }
    </div>
  )
}

export default InlineStyleControls
