import css from './FeatureItem.module.scss'

function FeatureItem ({
  name,
  value
}) {
  return (
    <div>
      <dt className={css.key}>
        { name }
      </dt>
      <dd
        dangerouslySetInnerHTML={{ __html: value }}
        className={css.value}
      />
    </div>
  )
}

export default FeatureItem
