import css from './RibbonProduct.module.scss'
import Ribbon from 'components/Templates/RibbonTemplate/RibbonTemplate'
import Container from 'components/Templates/Container/Container'

function RibbonProduct ({
  className,
  heading,
  image,
  period
}) {
  return (
    <Ribbon
      className={className}
      image={image}
    >
      <Container className={css.content}>
        <h2 className={css.heading}>
          { heading }
        </h2>
        <span className={css.duration}>
          { `${period.from} - ${period.to}` }
        </span>
      </Container>
    </Ribbon>
  )
}

export default RibbonProduct