import { memo } from 'react'
import css from './Input.module.scss'
import classnames from 'classnames'
import IconSearch from 'assets/icons/IconSearch'

const DefaultInput = memo(({className, children, formRef, ...props}) => (
  <label className={classnames(css.wrapper, className)}>
    <input
      className={css.input}
      ref={formRef}
      {...props}
    />
    { children }
  </label>
))

export const InputVariants = {
  SEARCH: `SEARCH`
}

function Input ({
  className,
  variant,
  ...props
}) {
  switch (variant) {
    case InputVariants.SEARCH: {
      return (
        <DefaultInput
          className={classnames(className, css.inputSearch)}
          {...props}
        >
          <IconSearch
            className={css.iconSearch}
          />
        </DefaultInput>
      )
    }
    default: {
      return (
        <DefaultInput className={className}
          {...props}
        />
      )
    }
  }
}

export default memo(Input)