import { useDispatch } from 'react-redux'
import { useEffect, memo } from 'react'
import { initFirebase } from 'store/actions'
import withModal from 'hoc/withModal'

function ApplicationInit({ children }) {
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(initFirebase())
  }, [dispatch])

  return children
}

const ApplicationInitMemoized = memo(ApplicationInit)

export default withModal(ApplicationInitMemoized)
