import { Fragment, memo, useMemo } from 'react'
import css from './SliderSuppliers.module.scss'
import classnames from 'classnames'
import SliderProvider from 'components/Molecules/SliderProvider/SliderProvider'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { SwiperSlide } from 'swiper/react'
import SlideLogo from 'components/Atoms/slides/SlideLogo/SlideLogo'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'

function SliderSuppliers ({
  className,
  data,
}) {
  const {
    list,
    heading
  } = data
  
  const slides = useMemo(() => {
    return list.map((item, index) => (
      <SwiperSlide key={index}>
        <SlideLogo {...item} />
      </SwiperSlide>
    ))
  }, [list])
  
  return (
    <div className={classnames(css.wrapper, className)}>
      <Heading
        tag={HeadingTypes.H2}
        style={HeadingTypes.H3}
        isCapitalized
      >
        { heading }
      </Heading>
      <SliderProvider
        slides={slides}
        autoplay={{
          delay: 1,
          pauseOnMouseEnter: true,
          disableOnInteraction: true
        }}
        speed={1000}
        loop={true}
        slidesPerView={4}
        className={css.slider}
      >
        {
          ({buttonPrev, buttonNext, isFirstSlide, isLastSlide}) => (
            <>
              <Button
                className={css.button}
                elRef={buttonPrev}
                variant={ButtonVariants.SLIDER_PREV_DARK}
                isDisabled={isFirstSlide}
              >
                Slide prev
              </Button>
              <Button
                className={classnames(css.button, css.buttonNext)}
                elRef={buttonNext}
                variant={ButtonVariants.SLIDER_NEXT_DARK}
                isDisabled={isLastSlide}
              >
                Slide next
              </Button>
            </>
          )
        }
      </SliderProvider>
    </div>
  )
}

export default memo(SliderSuppliers)