import { useRef, useEffect, useState } from 'react'
import css from './AnimatedLogo.module.scss'
import classnames from 'classnames'
import useIntersectionObserver from 'hooks/useIntersectionObserver'
import IconLogoOuter from 'assets/icons/IconLogoComponents/IconLogoOuter'
import IconLogoInner from 'assets/icons/IconLogoComponents/IconLogoInner'

function AnimatedLogo ({className}) {
  const wrapperRef = useRef(null)
  const [isShifted, setShiftedStatus] = useState(false)

  const {
    isVisible,
    unsubscribeObserver
  } = useIntersectionObserver({
    elementRef: wrapperRef
  })
  
  useEffect(() => {
    if (isVisible) {
      unsubscribeObserver()
    }
  }, [isVisible, unsubscribeObserver])

  useEffect(() => {
    function handleTransitionEnd (evt) {
      if (evt.propertyName === `opacity`) {
        setShiftedStatus(true)
        wrapperRef.current.removeEventListener(`transitionend`, handleTransitionEnd)
      }
    }

    const wrapper = wrapperRef.current
    wrapper.addEventListener(`transitionend`, handleTransitionEnd)
    
    return () => {
      if (wrapper) {
        wrapper.removeEventListener(`transitionend`, handleTransitionEnd)
      }
    }
  }, [])

  return (
    <div
      className={classnames(css.wrapper, className, {
        [css.wrapperVisible]: isVisible,
        [css.wrapperShifted]: isShifted
      })}
      ref={wrapperRef}
    >
      <IconLogoOuter className={css.iconOuter} />
      <IconLogoInner className={css.iconInner} />
    </div>
  )
}

export default AnimatedLogo
