import { memo } from 'react'
import NavigationLayout from 'components/Molecules/NavigationLayout/NavigationLayout'
import { headerNavData } from 'utils/dataAbout'
import { withRouter } from 'react-router-dom'
import NavigationTemplate from 'components/Templates/NavigationTemplate/NavigatonTemplate'

function Navigation ({history, lang}) {
  const activeItem = history.location.pathname

  return (
    <NavigationTemplate>
      {className => (
        <NavigationLayout
          className={className}
          list={headerNavData[lang]}
          activeItem={activeItem}
        />
      )}
    </NavigationTemplate>
  )
}

const NavigationMemoized = memo(Navigation)

export default withRouter(NavigationMemoized)
