import { memo, useCallback } from 'react'
import css from './LoginButton.module.scss'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { useDispatch, useSelector } from 'react-redux'
import { logOut, toggleModal } from 'store/actions'
import ModalLogin from 'components/Organisms/modals/ModalLogin/ModalLogin'
import ModalMessage from 'components/Organisms/modals/ModalMessage/ModalMessage'

function LoginButton ({
  className
}) {
  const userEmail = useSelector(store => store.auth.email)
  const isLoading = useSelector(store => store.auth.isLoggingIn)
  const dispatch = useDispatch()
  const isAuthorized = Boolean(userEmail)
  
  const handleClickButton = useCallback(() => {
    dispatch(toggleModal(<ModalLogin />, {
      isContentOnly: true
    }))
  }, [dispatch])
  
  const handleLogout = useCallback(() => {
    dispatch(toggleModal(
      <ModalMessage
        className={css.modal}
        heading={`Вы уверены, что хотите выйти?`}
        description={`В случае выхода из системы вы потеряете возможность создания и редактирования записей в блоге. Для восстановления доступа к этому функционалу потребуется повторная авторизация`}
        submitButtonHandler={() => {
          dispatch(toggleModal(null))
          dispatch(logOut())
        }}
      />
    ))
  }, [dispatch])
  
  return (
    <Button
      onClick={isAuthorized ? handleLogout : handleClickButton}
      className={className}
      variant={ButtonVariants.LOGIN}
      isLoading={isLoading}
    >
      { isAuthorized ? `${userEmail}` : `Личный кабинет` }
    </Button>
  )
}

export default memo(LoginButton)