import React, { memo } from 'react'
import useAuthStatus from 'hooks/useAuthStatus'
import { useSelector } from 'react-redux'
import useGlobalHeaderPadding from 'hooks/useGlobalHeaderPadding'
import { ParallaxProvider } from 'react-scroll-parallax'
import RoutesAdaptive from 'Pages/RoutesAdaptive'
import HeaderAdaptive from 'components/Organisms/Header/HeaderAdaptive'
import Footer from 'components/Organisms/Footer/Footer'
import { withRouter } from 'react-router-dom'
import { ROUTES_WITHOUT_GLOBAL_PADDING_TOP } from 'utils/const'

const AdaptiveApp = ({location}) => {
  const isAuth = useAuthStatus()
  const isPaddingRequired = !ROUTES_WITHOUT_GLOBAL_PADDING_TOP.includes(location.pathname)
  const paddingGlobal = useSelector(store => store.ui.globalPadding)
  
  const {
    setHeaderRef,
  } = useGlobalHeaderPadding()
  
  return (
    <ParallaxProvider>
      <div className="grid">
        <HeaderAdaptive
          setHeaderRef={setHeaderRef}
        />
        <main
          style={{
            paddingTop: `${isPaddingRequired ? paddingGlobal : 0}px`
          }}
        >
          <h1 className="visuallyHidden">
            Энергетический Стандарт - поставщик комплексных решений в энергетике
          </h1>
          <RoutesAdaptive isAuth={isAuth} />
        </main>
        <Footer />
      </div>
    </ParallaxProvider>
  )
}

export default memo(withRouter(AdaptiveApp))
