import { memo } from 'react'
import { useForm } from 'react-hook-form'
import useRenderFormItems from 'hooks/useRenderFormItems'

const Form = ({
  data,
  defaultValues = {},
  submitForm,
  children
}) => {
  const { control, handleSubmit, ...methods } = useForm({
    defaultValues
  })
  const items = useRenderFormItems({
    control,
    list: data
  })

  return (
    <form onSubmit={handleSubmit(data => submitForm(data, {
      ...methods
    }))}>
      {children({
        items,
        control,
        ...methods
      })}
    </form>
  )
}

const FormMemoized = memo(Form)

export default FormMemoized