import { LangOptions } from 'utils/const'
import {
  SET_GLOBAL_PADDING,
  SET_LANG,
  TOGGLE_MODAL
} from 'store/actions/actionTypes'
import { updateObject } from 'utils'

const initialState = {
  modal: {
    content: null,
    options: null
  },
  lang: LangOptions.RU,
  globalPadding: 120
}

export function uiReducer (state = initialState, {type, payload}) {
  switch (type) {
    case SET_LANG: return updateObject(state, { lang: payload })
    case TOGGLE_MODAL:
      return updateObject(state, { modal: {
        content: payload.content,
        options: payload.options
      }})
    case SET_GLOBAL_PADDING:
      return updateObject(state, {
        globalPadding: payload
      })
    default: return state
  }
}
