import {
  UPDATE_PROJECTS_DATA,
  UPDATE_PROJECTS_QUERY, UPDATE_PROJECTS_YEAR
} from 'components/Organisms/ProjectsDataProvider/controller/actionTypes'

export function updateProjectsData (data) {
  return {
    type: UPDATE_PROJECTS_DATA,
    payload: data
  }
}

export function updateProjectsQuery (query) {
  return {
    type: UPDATE_PROJECTS_QUERY,
    payload: query
  }
}

export function updateProjectsYear (year) {
  return {
    type: UPDATE_PROJECTS_YEAR,
    payload: year
  }
}