import {useEffect, useReducer, memo, useCallback} from 'react'
import { projectsReducer } from 'components/Organisms/ProjectsDataProvider/controller/reducer'
import { projectsList } from 'utils/dataProjects'
import {updateProjectsData} from 'components/Organisms/ProjectsDataProvider/controller/actions'
import useDebounce from 'hooks/useDebounce'

function ProjectsDataProvider ({children}) {
  const PROJECTS_INITIAL_STATE = {
    data: projectsList,
    query: ``,
    selectedYear: null
  }
  
  const [state, dispatch] = useReducer(projectsReducer, PROJECTS_INITIAL_STATE)
  
  const {
    data,
    query,
    selectedYear
  } = state
  
  const queryDebounced = useDebounce(query, 500)
  
  const checkObjectForSubstring = useCallback((obj, query) => {
    return Object.values(obj)
      .filter(item => {
        if (typeof item !== `object`) {
          return `${item}`.toLowerCase().includes(query.toLowerCase())
        } else {
          return checkObjectForSubstring(item, query)
        }
      })
  }, [])
  
  const filterData = useCallback(query => {
    if (!query) {
      dispatch(updateProjectsData(PROJECTS_INITIAL_STATE.data))
    }
    
    if (query) {
      const filteredData = PROJECTS_INITIAL_STATE.data.filter(item => {
        // return checkObjectForSubstring(item, query)
        return Object.values(item)
          .filter(item => {
            if (typeof item === `string` || typeof item === `number`) {
              return `${item}`.toLowerCase().includes(query.toLowerCase())
            }

            if (typeof item === `object`) {
              return Object.values(item).filter(item => `${item}`.toLowerCase().includes(query.toLowerCase())).length > 0
            }
            
            return true
          })
          .length > 0
      })
      
      dispatch(updateProjectsData(filteredData))
    }
  }, [PROJECTS_INITIAL_STATE.data])
  
  useEffect(() => {
    filterData(queryDebounced)
  }, [queryDebounced, filterData])
  
  return children({
    data,
    isLastDataItem: state.data.length === PROJECTS_INITIAL_STATE.data.length,
    query,
    selectedYear,
    dispatch
  })
}

export default memo(ProjectsDataProvider)