import { memo } from 'react'
import css from './Button.module.scss'
import classnames from 'classnames'
import IconSliderArrow from 'assets/icons/IconSliderArrow'
import Link from 'components/Atoms/Link/Link'
import IconArrowButton from 'assets/icons/IconArrowButton'
import IconLoad from 'assets/icons/IconLoad'
import IconEdit from 'assets/icons/IconEdit'
import IconUser from 'assets/icons/IconUser'
import IconSpinner from 'assets/icons/IconSpinner'
import IconClose from 'assets/icons/IconClose'
import IconBurger from 'assets/icons/IconBurger'

export const ButtonVariants = {
  SLIDER_PREV: `SLIDER_PREV`,
  SLIDER_PREV_DARK: `SLIDER_PREV_DARK`,
  SLIDER_NEXT: `SLIDER_NEXT`,
  SLIDER_NEXT_DARK: `SLIDER_NEXT_DARK`,
  FILLED: `FILLED`,
  BORDERED: `BORDERED`,
  ARROW: `ARROW`,
  ARROW_REVERSE: `ARROW_REVERSE`,
  MORE: `MORE`,
  EDIT: `EDIT`,
  LOGIN: `LOGIN`,
  CLOSE: `CLOSE`,
  BURGER: `BURGER`
}

const DefaultButton = memo(({
  className,
  children,
  elRef,
  isLoading,
  ...extraProps
}) => {
  const TagName = extraProps?.link
    ? Link
    : `button`
  
  return (
    <TagName
      className={classnames(css.button, className, {
        [css.buttonLoading]: isLoading
      })}
      ref={elRef}
      {...extraProps}
    >
      { children }
      {isLoading && <IconSpinner className={css.spinner} />}
    </TagName>
  )
})

function Button ({
  className,
  variant,
  isDisabled,
  children,
  ...extraProps
}) {
  switch (variant) {
    case ButtonVariants.SLIDER_PREV:
    case ButtonVariants.SLIDER_PREV_DARK:
    case ButtonVariants.SLIDER_NEXT_DARK:
    case ButtonVariants.SLIDER_NEXT: {
      const isDark = variant === ButtonVariants.SLIDER_PREV_DARK ||
        variant === ButtonVariants.SLIDER_NEXT_DARK
      
      const isNext = variant === ButtonVariants.SLIDER_NEXT ||
        variant === ButtonVariants.SLIDER_NEXT_DARK
      
      return (
        <DefaultButton
          className={classnames(className, css.buttonSlider, {
            [css.buttonSliderNext]: isNext,
            [css.buttonSliderDark]: isDark,
            [css.buttonSliderDisabled]: isDisabled
          })}
          {...extraProps}
        >
          <IconSliderArrow className={css.iconArrow} />
          { children }
        </DefaultButton>
      )
    }
    
    case ButtonVariants.ARROW_REVERSE:
    case ButtonVariants.ARROW: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonArrow, {
            [css.buttonArrowReverse]: variant === ButtonVariants.ARROW_REVERSE
          })}
          {...extraProps}
        >
          <span className={css.label}>
            { children }
          </span>
          <span className={css.wrapperArrow}>
            <IconArrowButton className={css.iconArrow} />
          </span>
        </DefaultButton>
      )
    }
    
    case ButtonVariants.MORE: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonMore)}
          {...extraProps}
        >
          { children }
          <IconLoad className={css.iconLoad} />
        </DefaultButton>
      )
    }
    
    case ButtonVariants.EDIT: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonEdit)}
          {...extraProps}
        >
          { children }
          <IconEdit className={css.iconEdit} />
        </DefaultButton>
      )
    }
    
    case ButtonVariants.LOGIN: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonLogin)}
          {...extraProps}
        >
          <IconUser className={css.iconUser} />
          { children }
        </DefaultButton>
      )
    }
    
    case ButtonVariants.CLOSE: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonClose)}
          {...extraProps}
        >
          <IconClose className={css.iconClose} />
          { children }
        </DefaultButton>
      )
    }
    
    case ButtonVariants.BURGER: {
      return (
        <DefaultButton
          className={classnames(className, css.buttonBurger)}
          {...extraProps}
        >
          <IconBurger className={css.iconBurger} />
          Toggle navigation menu
        </DefaultButton>
      )
    }
    
    default: {
      return (
        <DefaultButton
          className={classnames(className, {
            [css.buttonFilled]: variant === ButtonVariants.FILLED,
            [css.buttonBordered]: variant === ButtonVariants.BORDERED,
          })}
          {...extraProps}
        >
          { children }
        </DefaultButton>
      )
    }
  }
}

export default memo(Button)