import { LOGGING_IN_STATUS, UPDATE_AUTH_DATA } from 'store/actions/actionTypes'
import { getAuth } from 'firebase/auth'

export function updateAuthData (data) {
  return ({
    type: UPDATE_AUTH_DATA,
    payload: data
  })
}

export function logOut () {
  return dispatch => {
    dispatch({
      type: LOGGING_IN_STATUS,
      payload: true
    })
    
    const auth = getAuth()
    auth.signOut()
      .then(() => {
        dispatch({
          type: UPDATE_AUTH_DATA,
          payload: {
            email: null,
          }
        })
  
        dispatch({
          type: LOGGING_IN_STATUS,
          payload: false
        })
      })
      .catch(() => {
        dispatch({
          type: LOGGING_IN_STATUS,
          payload: false
        })
      })
  }
}

export function setLoggingIn (status) {
  return {
    type: LOGGING_IN_STATUS,
    payload: status
  }
}