import css from './ModalMessage.module.scss'
import classnames from 'classnames'
import { useDispatch } from 'react-redux'
import { toggleModal } from 'store/actions'
import ModalTemplate from 'components/Templates/ModalTemplate/ModalTemplate'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'

const ModalMessage = ({
  className,
  heading,
  description,
  error,
  isCancelButtonRequired = true,
  cancelButtonLabel = `Отмена`,
  cancelButtonHandler = () => {},
  isSubmitButtonRequired = true,
  submitButtonLabel = `Подтвердить`,
  submitButtonHandler = () => {}
}) => {
  const dispatch = useDispatch()

  function renderError () {
    if (!error) return null

    return error
  }

  function handleClickCancel () {
    dispatch(toggleModal(null))
  }

  return (
    <ModalTemplate
      className={classnames(css.wrapper, className)}
    >
      {heading &&
        <Heading
          className={css.heading}
          style={HeadingTypes.H3}
          tag={HeadingTypes.H3}
        >
          {heading}
        </Heading>
      }
      {description &&
        <p className={css.description}>
          { description }
        </p>
      }
      {error && renderError()}
      <div className={css.controls}>
        {isCancelButtonRequired &&
          <Button
            onClick={() => {
              cancelButtonHandler()
              handleClickCancel()
            }}
            className={classnames(css.button, {
              [css.buttonCentered]: !isSubmitButtonRequired
            })}
            variant={ButtonVariants.BORDERED}
          >
            { cancelButtonLabel }
          </Button>
        }
        {isSubmitButtonRequired &&
          <Button
            onClick={submitButtonHandler}
            className={classnames(css.button, {
              [css.buttonCentered]: !isCancelButtonRequired
            })}
            variant={ButtonVariants.FILLED}
          >
            { submitButtonLabel }
          </Button>
        }
      </div>
    </ModalTemplate>
  )
}

export default ModalMessage
