import { memo, useCallback, useMemo, useState } from 'react'
import css from './HeaderAdaptive.module.scss'
import Container from 'components/Templates/Container/Container'
import HeaderAdaptiveMenu from 'components/Organisms/HeaderAdaptiveMenu/HeaderAdaptiveMenu'
import NavigationLayout from 'components/Molecules/NavigationLayout/NavigationLayout'
import { headerNavData } from 'utils/dataAbout'
import useHeaderTypeController from 'hooks/useHeaderTypeController'
import { useSelector } from 'react-redux'
import { withRouter } from 'react-router-dom'
import NavigationTemplate from 'components/Templates/NavigationTemplate/NavigatonTemplate'
import Button, { ButtonVariants } from 'components/Atoms/Button/Button'
import { HOME_PAGE } from 'utils/routes'
import Logo from 'components/Atoms/Logo/Logo'
import LangSelectorAdaptive from 'components/Organisms/LangSelector/LangSelectorAdaptive'

function HeaderAdaptive ({setHeaderRef, history}) {
  const [isOpen, setOpenStatus] = useState(false)
  const lang = useSelector(store => store.ui.lang)
  const activeItem = history.location.pathname
  const isAboutCompany = activeItem === HOME_PAGE
  
  const {className} = useHeaderTypeController()
  
  const handleClickBurger = useCallback(() => {
    setOpenStatus(prevState => !prevState)
  }, [])
  
  const handleCloseMenu = useCallback(() => {
    setOpenStatus(false)
  }, [])
  
  const burger = useMemo(() => {
    return (
      <Button
        onClick={handleClickBurger}
        variant={ButtonVariants.BURGER}
      />
    )
  }, [handleClickBurger])
  
  const buttonClose = useMemo(() => {
    return (
      <Button
        onClick={handleCloseMenu}
        className={css.buttonClose}
        variant={ButtonVariants.CLOSE}
      >
        Close menu
      </Button>
    )
  }, [handleCloseMenu])
  
  const logo = useMemo(() => {
    if (isAboutCompany) {
      return (
        <span className={css.companyName}>
          Energy Standard
        </span>
      )
    }
    
    return (
      <Logo className={css.logo} isLogoOnly />
    )
  }, [isAboutCompany])
  
  return (
    <>
      <header className={className} ref={setHeaderRef}>
        <Container className={css.container}>
          { burger }
          { logo }
          <LangSelectorAdaptive />
        </Container>
      </header>
      <HeaderAdaptiveMenu isOpen={isOpen} setOpenStatus={setOpenStatus}>
        <>
          <NavigationTemplate>
            {
              className => (
                <NavigationLayout
                  className={className}
                  list={headerNavData[lang]}
                  activeItem={activeItem}
                  handleClickItem={handleCloseMenu}
                />
              )
            }
          </NavigationTemplate>
          { buttonClose }
        </>
      </HeaderAdaptiveMenu>
    </>
  )
}

export default memo(withRouter(HeaderAdaptive))