import {memo, useMemo} from 'react'
import ProjectCard from 'components/Molecules/ProjectCard/ProjectCard'
import ProjectsListTemplate from 'components/Templates/ProjectsListTemplate/ProjectsListTemplate'
import NoDataMessage from 'components/Atoms/NoDataMessage/NoDataMessage'

function ProjectsList ({
  className,
  data
}) {
  const items = useMemo(() => {
    if (Array.isArray(data) && data.length === 0) {
      return (
        <NoDataMessage>
          По Вашему запросу не обнаружено проектов
        </NoDataMessage>
      )
    }
    
    return data
      .map((item, index) => (
        <ProjectCard
          isOdd={index % 2 === 0}
          key={index}
          {...item}
        />
      ))
  }, [data])
  
  return (
    <ProjectsListTemplate className={className}>
      { items }
    </ProjectsListTemplate>
  )
}

export default memo(ProjectsList)