import React, { useRef } from 'react'
import css from './InputEditor.module.scss'
import {
  Editor,
  EditorState,
  ContentState,
  RichUtils,
  getDefaultKeyBinding,
  convertFromHTML
} from 'draft-js'
import IconInsertLink from 'assets/icons/IconInsertLink'
import useResetHeadingStyles from 'hooks/editor/useResetHeadingStyles'
import useBlockStyleControls from 'hooks/editor/useBlockStyleControls'
import useCreateLinkDecorator from 'hooks/editor/useCreateLinkDecorator'
import InlineStyleControls from 'components/Molecules/InputEditor/InlineStyleControls/InlineStyleControls'
import StyleButton from 'components/Molecules/InputEditor/StyleButton/StyleButton'

const InputEditor = ({className, onChange, value: editorValue, defaultValue = ``}) => {
  const decorator = useCreateLinkDecorator()
  const editorState = editorValue || EditorState.createWithContent(
    ContentState.createFromBlockArray(
      convertFromHTML(defaultValue)
    ),
    decorator
  )

  const editorRef = useRef(null)
  const wrapperRef = useRef(null)
  const keyBindingsObserver = useRef([])

  useResetHeadingStyles({
    onChange,
    observer: keyBindingsObserver.current
  })

  function getCustomKeyBindings (evt) {
    const isHandled = keyBindingsObserver.current.reduce((flag, fn) => {
      flag = !flag && fn(evt, editorState)
      return flag
    }, false)

    if (!isHandled) {
      return getDefaultKeyBinding(evt)
    }
  }

  function handleKeyCommand (command) {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      onChange(newState);
      return true;
    }
    return false;
  }

  function toggleInlineStyle (inlineStyle) {
    onChange(
      RichUtils.toggleInlineStyle(
        editorState,
        inlineStyle
      )
    )
  }

  function handleClickWrapper () {
    editorRef.current.focus()
  }
  
  function getSelectedText () {
    let selection = editorState.getSelection()
    const anchorKey = selection.getAnchorKey()
    const currentContent = editorState.getCurrentContent()
    const currentBlock = currentContent.getBlockForKey(anchorKey)

    const start = selection.getStartOffset()
    const end = selection.getEndOffset()
    return currentBlock.getText().slice(start, end)
  }

  function convertTextToLink () {
    const text = getSelectedText()

    const contentState = editorState.getCurrentContent()
    const contentStateWithEntity = contentState.createEntity(
      'LINK',
      'MUTABLE',
      {url: text}
    )
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey()
    const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity })

    onChange(RichUtils.toggleLink(
      newEditorState,
      newEditorState.getSelection(),
      entityKey
    ))
  }

  const {
    blockStyleButtonsComponent,
    getBlockStyle
  } = useBlockStyleControls({editorState, onChange})

  return (
    <div className={className}>
      { blockStyleButtonsComponent }
      <InlineStyleControls
        editorState={editorState}
        onToggle={toggleInlineStyle}
      >
        <StyleButton
          onClick={convertTextToLink}
          renderIcon={className => <IconInsertLink className={className} /> }
        />
      </InlineStyleControls>
      <div className={css.wrapper} onClick={handleClickWrapper} ref={wrapperRef}>
        <Editor
          editorState={editorState}
          onChange={onChange}
          blockStyleFn={getBlockStyle}
          keyBindingFn={getCustomKeyBindings}
          handleKeyCommand={handleKeyCommand}
          placeholder="Введите текст..."
          spellCheck={true}
          ref={editorRef}
        />
      </div>
    </div>
  )
}

export default InputEditor
