import LangSelectorLayout from 'components/Molecules/LangSelectorLayout/LangSelectorLayout'
import { LangOptions } from 'utils/const'
import { useDispatch, useSelector } from 'react-redux'
import { setLang } from 'store/actions'

function LangSelector ({className}) {
  const dispatch = useDispatch()
  const activeLang = useSelector(store => store.ui.lang)

  function handleClickLang (lang) {
    dispatch(setLang(lang))
  }

  return (
    <LangSelectorLayout
      className={className}
      list={Object.values(LangOptions)}
      onClick={handleClickLang}
      activeLang={activeLang}
    />
  )
}

export default LangSelector
